export const LookupPage = () => {
  const [activeHeader, setActiveHeader] = useState('Codes');
  const [activeClass, setActiveClass] = useState(0);
  const [activeItem, setActiveItem] = useState('ICD-10');


  // const [icd10Response, setICD10Response] = useState<any>(undefined);
  // const [icd10CodeData, setICD10CodeData] = useState<any>(undefined);
  const [icdCategories, setICDCategories] = useState<string[]>([]);
  const [icdCategoryDropdown, setICDCategoryDropdown] = useState<any>(undefined);
  // const [icd10ListClicked, setICD10ListClicked] = useState<number>(-1);


  // const [cptResponse, setCPTResponse] = useState<any>(undefined);
  // const [cptCodeData, setCPTCodeData] = useState<any>(undefined);
  const [cptCategories, setCPTCategories] = useState<string[]>([]);
  const [cptCategoryRanges, setCPTCategoryRanges] = useState<string[]>([]);
  const [cptCategoryDropdown, setCPTCategoryDropdown] = useState<any>(undefined);
  // const [cptListClicked, setCPTListClicked] = useState<number>(-1);

  const classes = useStyles();

  const activeSideBarItem = (item) => {
    setActiveItem(item);
  }

  const handleMenuClick = (item, index) => {
    setActiveHeader(item);
    setActiveClass(index)
  }

  return (
    <div className={classes.main}>
      <Grid
        container
        justify="space-between"
        alignItems="center"
        spacing={0}
        className={classes.gridContainer}
      >
        <Grid item>
          <Typography className={classes.headerTitle}>Lookup</Typography>
        </Grid>
      </Grid>
      <GridHorizontalDivider height="15px" />








      {/* HEADER */}

      <Grid item xs={6}>
        <ul style={{ padding: '0', margin: '0' }}>
          {
            page_tab_titles.map((item, i) => (
              <li className={classes.menuList}>
                <Link className={classNames(classes.menuLink, activeClass === i ? classes.LinkActive : null)} onClick={() => handleMenuClick(item, i)}>{item}</Link>
              </li>
            ))
          }
        </ul>
      </Grid>











      <Grid container justify="space-between" spacing={0}>
        <GridHorizontalDivider height="30px" />


        {/* SIDE MENU */}
        <Grid item xs={2} className={classes.sideBarContainer}>
          <SideMenu
            items={sideBarItems[activeHeader]}
            activeItems={activeSideBarItem}
            reset={activeHeader}
          ></SideMenu>
        </Grid>


        {/* SECTIONS */}
        {activeItem === 'ICD-10' && activeHeader === 'Codes' ? (
          <Grid item xs={12} md={10}>
            <ICD10Section
              classes={classes}

              // icd10Response={icd10Response}
              // setICD10Response={setICD10Response}
              // icd10CodeData={icd10CodeData}
              // setICD10CodeData={setICD10CodeData}
              icdCategories={icdCategories}
              setICDCategories={setICDCategories}
              icdCategoryDropdown={icdCategoryDropdown}
              setICDCategoryDropdown={setICDCategoryDropdown}
              // icd10ListClicked={icd10ListClicked}
              // setICD10ListClicked={setICD10ListClicked}


            />
          </Grid>

        ) : activeItem === 'CPT' && activeHeader === 'Codes' ? (
          <Grid item xs={12} md={10}>
            <CPTSection
              classes={classes}
              // cptResponse={cptResponse}
              // setCPTResponse={setCPTResponse}
              // cptCodeData={cptCodeData}
              // setCPTCodeData={setCPTCodeData}
              cptCategories={cptCategories}
              setCPTCategories={setCPTCategories}
              cptCategoryRanges={cptCategoryRanges}
              setCPTCategoryRanges={setCPTCategoryRanges}
              cptCategoryDropdown={cptCategoryDropdown}
              setCPTCategoryDropdown={setCPTCategoryDropdown}
              // cptListClicked={cptListClicked}
              // setCPTListClicked={setCPTListClicked}
            />
          </Grid>
        ) : null}
      </Grid>
    </div>
  );
};

const mapStateToProps = (state: Store) => state.home;

const mapDispatchToProps = (dispatch: Dispatch) => ({
  Map: bindActionCreators(LookupActions.Map, dispatch),
});

export { mapStateToProps, mapDispatchToProps };
